@import "colors";

.orderDetails{
    background-color: $pink;
    width:100%;

    >main{
        background-color: white;
        max-width: 900px;
        margin: auto;
        padding: 2rem;
        overflow-x: auto;
        >h1{
            text-transform: uppercase;
            text-align: center;
            font:100 3rem "Roboto";
            margin: 3rem;
        }
        >div{
            margin: 2rem 0;
            text-align: center;
            >h1{
            font-size: 1.8rem;
            text-transform: uppercase;
            letter-spacing: 5px;

            }
            >p{
                font-weight: 200;
                white-space: nowrap;
                letter-spacing: 2px;
                >b{
                    margin-right: 5px;
                }
            }
        }
            >article{
                >h1{
                    font-size: 1.8rem;
                    text-transform: uppercase;
                    letter-spacing: 5px;
                    margin: 2rem 0;
                    
                }
                >div{
                    background-color: rgb(246,246,246);
                    display: flex;
                    margin: 1rem;
                    align-items: center;
                    justify-content: space-between;
                    padding: 1rem;
                    border-radius: 5px;
                    >h4{
                        font-weight: 200;
                        letter-spacing: 2px;
                    }
                }
            }
        }
        
    }


    @media screen  and (max-width:600px)
    {
        .orderDetails>main>div{
            >h1{
                font-size: 1.4rem;
            }
            >p{
                letter-spacing: 1px;
                font-size: 0.9rem;
                margin: 2rem 0;
            }
        
        }
    }